.box{
    margin-left: 20vw;
    margin-right: 20vw;
    margin-top: 10vh ;
  
    display: grid;
    grid-template-columns: 50% 50%   ;
    grid-template-rows: 50% 50%;

    color: #c2c2c2;
    /* color: #c3c3c3; */
   
 
    border-bottom: 0.1px solid #8b8b8b69;
    border-top: 0.1px solid #8b8b8b69;

  }

 /* -----------------------------------------  */
.one{
  margin-bottom: 5em;
